import React, { Component } from 'react';
import { Result } from 'antd';
import { isAdvancedBrowser } from '@/common/env';
import { ChromeDownloadLink } from '@/common/utils';
import Styles from './index.module.less';

export default class ErrorBoundary extends Component {
	state = {
		hasError: false,
		error: null,
		errorInfo: null,
	};
	static getDerivedStateFromError(error) {
		//  state  UI
		return {
			hasError: true,
			error,
		};
	}

	componentDidCatch(error, errorInfo) {
		// TODO: Radar

		this.setState({
			error,
			errorInfo,
		});
	}

	render() {
		const { hasError, error, errorInfo } = this.state;
		if (!hasError) {
			return this.props.children;
		}
		return (
			<Result
				className={Styles['main-error-boundary']}
				status="warning"
				title=""
				subTitle={
					isAdvancedBrowser ? (
						'Id'
					) : (
						<>
							chrome
							<a
								target="_blank"
								rel="noreferrer noopener"
								href={ChromeDownloadLink}
							>
								
							</a>
						</>
					)
				}
			/>
		);
	}
}
