import ReactDOM from 'react-dom';
import React from 'react';
import { isMicro } from './common/env';
import { DLMicroContext } from './common/context';
import App from './App';

const getRootContainer = (container: any) => {
  return container ? container.querySelector('#root') : document.getElementById('root');
};
let container: any;
/**
 * bootstrap  mount  bootstrap
 *  unmount 
 */
async function bootstrap() {}
/**
 *  mount 
 */
async function mount(props: any) {
  ReactDOM.render(
    <React.StrictMode>
      <DLMicroContext.Provider value={props}>
        <App />
      </DLMicroContext.Provider>
      ,
    </React.StrictMode>,
    getRootContainer(props.container),
  );
  container = getRootContainer(props.container);
}
/**
 *  / 
 */
async function unmount(props: any) {
  ReactDOM.unmountComponentAtNode(getRootContainer(props.container));
  container = null;
}
/**
 *  loadMicroApp 
 */
async function update(props: any) {
  ReactDOM.render(
    <DLMicroContext.Provider value={props}>
      <App />
    </DLMicroContext.Provider>,
    container,
  );
}

!isMicro() && mount({});

// Hot Module Replacement (HMR) - Remove this snippet to remove HMR.
// Learn more: https://snowpack.dev/concepts/hot-module-replacement
// if (import.meta.hot) {
// 	import.meta.hot?.accept?.();
// }

export { unmount, update, mount, bootstrap };
