/**
 * host
 * @param host
 * @returns boolean
 */
export function isNotPureHost(host: string) {
  const isValidHostWithProtocal = /^https?:\/\//.test(host);
  const isValidHostWithoutProtocal = /^\/\//.test(host);
  return isValidHostWithProtocal || isValidHostWithoutProtocal;
}
/**
 * window._CDN_HOST_CdnHost
 * @param cdnUrl
 * @returns
 */
export const repacleCDNHost: (cdnUrl: string) => string = (cdnUrl: string) => {
  try {
    if (typeof cdnUrl === 'string' && !!window._CDN_HOST_) {
      const url: any = new URL(cdnUrl);
      let host = window._CDN_HOST_;
      // window._CDN_HOST_  https://ali-ec.static.yximgs.com  //ali-ec.static.yximgs.com 
      if (isNotPureHost(host)) {
        // host ali-ec.static.yximgs.com
        //@ts-ignore
        host = new URI(window._CDN_HOST_).hostname();
      }
      url.hostname(host);
      return url.toString();
    }
  } catch (e) {
    return cdnUrl;
  }

  return cdnUrl;
};

/**
 * Chrome
 */
export const ChromeDownloadLink = (() => {
  const cdnUrlPrefix = '//ali2.a.kwimgs.com/udata/pkg/eshop';
  const link: string = /macintosh|mac os x/i.test(navigator.userAgent.toLocaleLowerCase())
    ? `${cdnUrlPrefix}/googlechrome-mac.dmg`
    : `${cdnUrlPrefix}/83.0.4103.106_chrome64_stable_windows_installer.exe`;

  return repacleCDNHost(link);
})();
